@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .shoppingCart{
              &__securePayment{
                &__headline{
                  form{
                    ul{
                      .state{
                        label{
                          input{
                            width: 62%;
                          }
                        }
                      }
                    }
                  }
                }
              }
              &__discount{
                &__coupon,
                &__certificate{
                  ul{
                    li{
                      input{
                        width: 370px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .aside{
              display: block;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 770px), screen and (max-device-width: 770px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .shoppingCart{
              &__items{
                div{
                  ul{
                    flex-wrap: wrap;
                    justify-content: flex-end;
                    li{
                      &:nth-child(1){
                        width: 20%;
                        text-align: center;
                      }
                      &:nth-child(2){
                        width: 80%;
                      }
                    }
                    .quantity,
                    .price{
                      margin: 0 15px;
                    }
                  }
                }
              }
              &__discount{
                &__coupon,
                &__certificate{
                  ul{
                    li{
                      input{
                        width: 270px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 710px), screen and (max-device-width: 710px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .shoppingCart{
              &__securePayment{
                &__headline{
                  form{
                    ul{
                      width: 100%;
                      li{
                        p{
                          display: none;
                        }
                        label{
                          width: 100%;
                          ::-webkit-input-placeholder{
                            color: #525252;
                          }
                          ::-moz-placeholder{
                            color: #525252;
                          }
                          :-moz-placeholder{
                            color: #525252;
                          }
                          :-ms-input-placeholder{
                            color: #525252;
                          }
                        }
                      }
                      .state{
                        label{
                          select{
                            margin: 0 10px 0 0;
                          }
                          input{
                            width: 85.5%;
                          }
                        }
                      }
                      .selectPayment{
                        flex-wrap: wrap;
                        p{
                          display: block;
                        }
                        .important{
                          width: 100%;
                          text-align: center;
                          span{
                            display: inline-block;
                          }
                        }
                        label{
                          flex-direction: column;
                          align-items: center;
                          span{
                            display: none;
                          }
                          ul{
                            margin: 0;
                            li{
                              width: 100%;
                              p{
                                span{
                                  width: 100%;
                                  display: inline-block;
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 650px), screen and (max-device-width: 650px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .shoppingCart{
              &__discount{
                &__coupon,
                &__certificate{
                  ul{
                    li{
                      input{
                        width: 170px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 550px), screen and (max-device-width: 550px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .shoppingCart{
              &__items{
                div{
                  ul{
                    li{
                      &:nth-child(1){
                        width: 30%;
                      }
                      &:nth-child(2){
                        width: 70%;
                      }
                    }
                    .quantity{
                      margin: 15px 0 5px;
                      width: 100%;
                      justify-content: flex-end;
                    }
                    .price{
                      margin: 10px 5px;
                      width: 100%;
                      text-align: right;
                    }
                  }
                }
              }
              &__discount{
                &__coupon,
                &__certificate{
                  ul{
                    flex-wrap: wrap;
                    li{
                      margin: 5px 0;
                      padding: 0 15px;
                      width: 100%;
                      justify-content: center;
                      input{
                        width: 70%;
                      }
                      span{
                        width: 30%;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 400px), screen and (max-device-width: 400px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .shoppingCart{
              &__items{
                div{
                  ul{
                    li{
                      &:nth-child(1){
                        width: 40%;
                      }
                      &:nth-child(2){
                        width: 60%;
                      }
                    }
                  }
                }
              }
              &__discount{
                &__coupon,
                &__certificate,
                &__deliveryCost{
                  h6{
                    padding: 0;
                    text-align: center;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}